html {
  overflow-x: hidden;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  background-image: -webkit-gradient(linear, left top, right top, from(#0f121b86), to(#301f1f5e)),
    url("../src/assets/images/intro_background_v2.jpg");
  background-image: -webkit-linear-gradient(left, #0f121b86, #301f1f5e),
    url("../src/assets/images/intro_background_v2.jpg");
  background-image: -o-linear-gradient(left, #0f121b86, #301f1f5e),
    url("../src/assets/images/intro_background_v2.jpg");
  background-image: linear-gradient(to right, #0f121b86, #301f1f5e),
    url("../src/assets/images/intro_background_v2.jpg");
  background-size: cover;
  background-position: 40% 80%;
  background-attachment: fixed;
}

.container {
  width: 1100px;
  margin: 20px auto;
  padding-top: 20px;
}

@media (max-width: 1120px) {
  .container {
    width: 880px;
    margin: 20px auto;
  }
}

@media (max-width: 900px) {
  .container {
    width: 660px;
    margin: 20px auto;
  }
}

@media (max-width: 700px) {
  .container {
    width: 480px;
    margin: 20px auto;
  }
}

@media (max-width: 500px) {
  .container {
    width: 440px;
    margin: 20px auto;
  }
}

@media (max-width: 460px) {
  .container {
    width: 400px;
    margin: 20px auto;
  }
}

@media (max-width: 420px) {
  .container {
    width: 380px;
    margin: 20px auto;
  }
}

@media (max-width: 400px) {
  .container {
    width: 330px;
    margin: 20px auto;
  }
}

@media (max-width: 350px) {
  .container {
    width: 300px;
    margin: 20px auto;
  }
}

.clearfix {
  zoom: 1;
}
.clearfix:after {
  content: " ";
  clear: both;
  display: block;
  height: 0;
  visibility: hidden;
}

@media (min-width: 800px) {
  .mobileOnly {
    display: none;
  }
}

@media (max-width: 800px) {
  .desktopOnly {
    display: none;
  }
}



