.Intro {
  width: 100vw;
  overflow: hidden;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.7)), to(rgba(0, 0, 0, 0.7))),
    url("../../assets/images/intro_background_v4.jpg");
  background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url("../../assets/images/intro_background_v4.jpg");
  background-image: -o-linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url("../../assets/images/intro_background_v4.jpg");
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url("../../assets/images/intro_background_v4.jpg");
  background-size: cover;
  background-position: 40% 80%;
  background-attachment: fixed;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.Intro__Start__Header {
  font-family: "Permanent Marker", cursive;
  font-size: 90px;
  color: antiquewhite;
}

.Intro__Start__Text__Desktop {
  font-family: "Permanent Marker", cursive;
  font-size: 24px;
  color: #ff9000;
  margin-left: 200px;
  margin-right: 200px;
}

.Intro__Start__Text__Mobile {
  font-family: "Permanent Marker", cursive;
  font-size: 23px;
  color: #ff9000;
  margin-left: 20px;
  margin-right: 20px;
  display: none;
}

@media (max-width: 800px) {
  .Intro__Start__Header {
    font-size: 70px;
  }

  .Intro__Start__Text__Desktop {
    display: none;
  }

  .Intro__Start__Text__Mobile {
    display: block;
  }
}



