.Layout__MainHeader {
  font-family: "Permanent Marker", cursive;
  font-size: 90px;
  color: #fff0dd;
  margin: 0;
  padding: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
      -ms-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  width: 100%;
  top: 5px;
}

.Layout__UserGreeting {
  font-weight: bold;
  color: #fff0dd;
  font-size: 16px;
}

@media (max-width: 600px) {
  .Layout__MainHeader {
    font-size: 61px;
    top: 35px;
  }
}

@media (max-width: 300px) {
  .Layout__MainHeader {
    font-size: 30px;
  }
}



